import { Component, Prop, h, Host } from '@stencil/core';

export type CardFooterAlignment = 'left' | 'right' | 'space-between';

@Component({
  tag: 'bh-card-footer',
  styleUrl: 'bh-card-footer.css',
  shadow: true,
})
export class BhCardFooter {
  /**
   * Whether to show the divider above the footer
   */
  @Prop() showDivider: boolean = true;

  /**
   * Alignment of the footer content
   */
  @Prop() alignment: CardFooterAlignment = 'space-between';

  render() {
    const footerContentClasses = {
      'card-footer-content': true,
      [`card-footer-align-${this.alignment}`]: true,
    };

    return (
      <Host>
        <div class="card-footer">
          {this.showDivider && <div class="card-footer-divider" />}
          <div class={footerContentClasses}>
            <div class="card-footer-primary-actions">
              <slot name="primary-actions" />
            </div>
            <div class="card-footer-secondary-actions">
              <slot name="secondary-actions" />
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
